import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Planos_cronograma_auditoria } from '../../../modelo/Planos_cronograma_auditoria'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'planos_cronograma_auditoria-dados-edit',   templateUrl: 'planos_cronograma_auditoria_dadosEdit.component.html' }) export class Planos_cronograma_auditoriaDadosEditComponent implements OnInit {    planos_cronograma_auditoria: Planos_cronograma_auditoria;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.planos_cronograma_auditoria.Planos_cronograma_auditoria";                 let id = +params['id'];         this.planos_cronograma_auditoria = new Planos_cronograma_auditoria();         this.planos_cronograma_auditoria.cd_planos_cronograma_auditoria = id;         o.objetoJson = this.planos_cronograma_auditoria;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a planos_cronograma_auditoria. ';       }      this.planos_cronograma_auditoria = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


